@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Montserrat:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Montserrat:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}
body {
    background-color: #202124;
    color: #fff;
}

.container {
    max-width: 1092px;
    margin: 0 auto;
}
.header{display:flex;align-items:center;justify-content:center}.header .header-title{color:orange;font-family:'Lobster', cursive;font-size:50px;line-height:30px;text-align:center;cursor:pointer}.header .header-title .header-subtitle{font-size:20px;margin:15px 0 0 0}


.to-do-list__item{display:flex;align-items:center;justify-content:space-between;padding:10px;background-color:#292a2d;border:2px solid #ccc;border-radius:4px;margin-bottom:5px;overflow:hidden}.to-do-list__item:hover .close{transform:scale(1) rotate(45deg)}.to-do-list__item-text{display:flex;align-items:center}.close{min-width:30px !important;min-height:30px !important;border:2px solid orange;border-radius:50%;margin-right:10px;color:white;cursor:pointer;transition:all .3s;transform:scale(0) rotate(45deg)}.checkbox{min-width:30px !important;min-height:30px !important;-webkit-appearance:none;background-color:transparent;padding:9px;border:2px solid orange;border-radius:50%;margin:0 10px 0 0}.checkbox:hover{cursor:pointer;background-color:#202124}.checkbox:checked:after{content:'\2714';font-size:20px;position:absolute;transform:translate(-4px, -11px);color:white}.to-do-text{word-wrap:break-word;font-family:'Montserrat', sans-serif;font-size:30px}.to-do-text.line-through{text-decoration:line-through}

.to-do-list__item.add-item{display:flex;justify-content:space-between}.plus{min-width:30px !important;min-height:30px !important;border:2px solid orange;border-radius:50%;margin-right:10px;color:white;transition:all .3s}.plus.rotate{transform:rotate(-90deg)}.add-inputs{width:100%;display:flex;justify-content:space-between}.add-text{color:#fff;border:none;width:80%;border-bottom:2px solid #fff;background-color:#292a2d;font-family:'Montserrat', sans-serif;font-size:30px}.add-button{color:#fff;background-color:#292a2d;border:2px solid orange;border-radius:4px;margin-left:10px;font-family:'Montserrat', sans-serif;font-size:30px}.add-button:hover{background-color:orange;color:black;cursor:pointer}.add-button.btn-disabled{opacity:.4}.add-button.btn-disabled:hover{background-color:#292a2d;cursor:default;color:white}

